import { Box, Dialog, IconButton, useMediaQuery, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import useStyles from './style';
import { CloseIcon } from './icons/icon';
import { QRCodeCanvas } from 'qrcode.react';

const QRModal = props => {
  const { open, handleClose, value } = props;
  const classes = useStyles();
  const max375 = useMediaQuery('(max-width:375px)');

  const downloadQRCode = () => {

    const qrCodeURL = document
      .getElementById('qrContainer')
      .querySelector('canvas')
      .toDataURL('image/png')
      .replace('image/png', 'image/octet-stream');
    let aEl = document.createElement('a');
    aEl.href = qrCodeURL;
    aEl.download = 'QR_Code.png';
    document.body.appendChild(aEl);
    aEl.click();
    document.body.removeChild(aEl);
  };

  return (
    <Dialog onClose={handleClose} open={open} className={classes.dialog}>
      <Box className={classes.header}>
        <Box>Wallet QR Code</Box>
        <Box className={classes.icon} onClick={handleClose}
         style={{marginTop: ".4rem", marginRight: '5px'}}>
          <IconButton
            color="primary"
            aria-label="upload picture"
            component="span"
            className={classes.control_button}>
            <CloseIcon />
          </IconButton>
        </Box>
      </Box>
      <Box className={classes.top_divider} />
      <Box className={classes.qr_container} id="qrContainer">
        <QRCodeCanvas value={value} id="qrCodeEl" size={300} />
      </Box>
      {
        max375 ? 
        <Box className={classes.download_msg} 
          style={{
            fontSize: "16px", 
            textAlign: "center", 
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
          }}
        >
          <Box>Scan code or</Box>
          <Box onClick={downloadQRCode} className={classes.link}>download as an image</Box>
          <Box>to share</Box>
        </Box>
        :
        <Box className={classes.download_msg}>
          <Box>Scan code or</Box>
          <Box onClick={downloadQRCode} className={classes.link}>download as an image</Box>
          <Box>to share</Box>
        </Box>
      }
    </Dialog>
  );
};

export default QRModal;
