import UniswapContract from "config/abi/uniswap.json"

import { ContractInstance } from "./contractInstance";
import Web3 from "web3";
const ethHttpProvider = new Web3.providers.HttpProvider(process.env.REACT_APP_NODE_URL);
const web3 = new Web3(ethHttpProvider);

export const getAmountsOut = (contract_address, pair, amount) => {
  return new Promise(async resolve => {
    try {
      const contract = ContractInstance(web3, UniswapContract, contract_address)
      const result = await contract.methods
        .getAmountsOut(amount, pair)
        .call()
      resolve({
        status: true,
        data: result
      })
    } catch (e) {
      console.log(e)
      resolve({
        status: false,
        error: e
      });
    }
  });
}