import React, { useEffect, useRef, useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Box, Input, Button, Grid } from '@material-ui/core';
import { KeyboardArrowDown } from '@material-ui/icons';
import { TokenSelectModal } from 'components';
import { getBNValue, getCorrectDecValue } from 'web3/contracts';
import { UNISWAP_ADDRESS, USDT_ADDRESS, USDC_ADDRESS, WETH_ADDRESS, ETH_ADDRESS } from 'utils/constants';
import { getAmountsOut } from 'web3/uniswap';

const useStyles = makeStyles(theme => ({
  error_root: {
    border: `1px solid #FF8585`,
    borderRadius: 12,
    padding: 16,
    backgroundColor: theme.palette.wallet_button_color,
  },
  root: {
    border: `1px solid #B2B7FF`,
    borderRadius: 12,
    padding: 16,
    backgroundColor: theme.palette.wallet_button_color,
  },
  container: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center'
  },
  input: {
    marginLeft: theme.spacing(2),
    flexGrow: 1,
    fontSize: '24px',
    lineHeight: '16px',
    letterSpacing: '-0.05px',
    color: theme.palette.text_main,
    width: '150px'
  },
  tag: {
    color: theme.palette.text_sub,
    fontSize: 14,
    marginRight: theme.spacing(1)
  },
  tokenContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  non_selected_connect_button: {
    boxShadow: 'none',
    borderRadius: 9,
    border: `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.wallet_button_color,
    textTransform: 'none',
    padding: theme.spacing(1, 1.5),
    display: 'flex',
    justifyContent: 'space-between',
    '&:hover': {
      backgroundColor: theme.palette.wallet_button_color,
      boxShadow: 'none'
    },
  },
  connect_button: {
    padding: 0,
    boxShadow: 'none',
    borderRadius: 9,
    border: `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.wallet_button_color,
    textTransform: 'none',
    display: 'flex',
    justifyContent: 'space-between',
    '&:hover': {
      backgroundColor: theme.palette.wallet_button_color,
      boxShadow: 'none'
    }
  },
  imageBox: {
    width: 40,
    height: 40,
    marginRight: 10,
    backgroundColor: theme.palette.wallet_button_color,
    borderRadius: 9,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  image: {
    width: 20,
    height: 20,
  },
  title: {
  },
  icon: {
    marginRight: 4.5,
    display: 'flex',
    alignItems: 'center'
  },
  valueContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: 8
  },
  subText: {
    fontSize: 14,
    color: theme.palette.text_sub
  },
  error: {
    fontSize: 14,
    color: '#FF8585',
  }
}));

const TokenInput = props => {
  const { children, tokenInfo, handleChange, handleChangeAmount, isFrom } = props;
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  const getAmountOut = async (token) => {
    let toAddress = (token?.address == ETH_ADDRESS ? WETH_ADDRESS : token?.address);
    let res = 0;
    const result = await getAmountsOut(UNISWAP_ADDRESS, [toAddress, toAddress === USDT_ADDRESS ? USDC_ADDRESS : USDT_ADDRESS], getBNValue(1, token.decimals) )
    if (result.status) {
      res = getCorrectDecValue(result.data[1], 6, -1)
    }
    return res;
  }

  const onChangeInput = (e) => {
    let _tokenInfo = JSON.parse(JSON.stringify(tokenInfo));
    _tokenInfo.amount = e.target.value;
    handleChangeAmount(_tokenInfo);
  }

  const onChangeToken = async (item) => {
    let _tokenInfo = item;
    _tokenInfo.price = await getAmountOut(_tokenInfo)
    handleChange(_tokenInfo);
  }

  return (
    <Box className={(isFrom && tokenInfo?.symbol && ((tokenInfo?.amount || 0) > (tokenInfo?.balance || 0))) ?
      classes.error_root : classes.root}>
      <Box className={classes.container}>
        <Box>
          <Input
            className={classes.input}
            disableUnderline
            disabled={!isFrom}
            onChange={onChangeInput}
            placeholder={"0.0"}
            value={isFrom ? (tokenInfo?.amount || '') : (Number(tokenInfo?.amount || 0).toFixed(3))}
          />
        </Box>
        <Box>
          <Box className={classes.tokenContainer} onClick={() => setOpen(true)}>
            <Box className={classes.tag}>{isFrom ? 'From:' : 'To:'}</Box>
            <Button variant="contained" className={tokenInfo?.symbol ? classes.connect_button : classes.non_selected_connect_button}>
              {tokenInfo?.symbol ? (
                <>
                  <Box className={classes.imageBox}>
                    <img src={tokenInfo?.logoURI} className={classes.image} />
                  </Box>
                  <Box className={classes.title}>{tokenInfo?.symbol}</Box>
                </>
              ) : (
                <Box className={classes.title}>Select a token</Box>
              )}
              <Box className={classes.icon}>
                <KeyboardArrowDown />
              </Box>
            </Button>
          </Box>
        </Box>
      </Box>
      <Box className={classes.valueContainer}>
        {
          (isFrom && tokenInfo?.symbol && ((tokenInfo?.amount || 0) > (getCorrectDecValue(tokenInfo?.balance || 0, tokenInfo?.decimals || 0, -1)))) ?
          <Box className={classes.error}>
            {`Insufficient ${tokenInfo?.symbol} balance`}
          </Box>
          :
          <Box className={classes.subText}>
            { tokenInfo?.symbol ? `$${((tokenInfo?.price || 0) * (tokenInfo?.amount || 0)).toFixed(3)}` : '' }
          </Box>
        }
        <Box className={classes.subText}>
          { tokenInfo?.symbol ? `${tokenInfo?.symbol} balance: ${getCorrectDecValue(tokenInfo?.balance || 0, tokenInfo?.decimals, 3)}` : '' }
        </Box>
      </Box>
      <TokenSelectModal
        open={open}
        handleClose={() => setOpen(false)}
        handleSelectItem={(item) => onChangeToken(item)}
      />
    </Box>
  );
};

export default TokenInput;
