import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import useStyles from './style';

import { Box, Button, Dialog, Grid, IconButton } from '@material-ui/core';
import { setCurrentWallet, setWalletList } from 'store/actions/App';
import { useDispatch } from 'react-redux';
import { EmailInput } from 'components';
import { magic } from 'utils/connectors';
import { GoIcon, FacebookIcon, GoogleIcon, AppleIcon } from './icons/icons';
import useActiveWeb3React from "hooks/web3-react"

const ConnectMagicModal = props => {
  const {handleClose, open} = props;
  const { activate, deactivate, account, library, chainId } = useActiveWeb3React();
  const classes = useStyles();

  const [email, setEmail] = useState('');

  const dispatch = useDispatch()

  const checkWallet = (account, wallet) => {
    return wallet.address === account.toLowerCase();
  };

  const handleLoginWithEmail = async () => {
    activate(email)
  }

  const handleLoginWithSocial = async () => {
    await magic.oauth.loginWithRedirect({
      provider: 'google' /* 'google', 'facebook', 'apple', or 'github' */,
      redirectURI:  window.location.href,
    });

    let userMetadata = await magic.user.getMetadata();

    let walletList = JSON.parse(localStorage.getItem('wallets') || '[]');

    if (!walletList.find((item) => checkWallet(userMetadata.publicAddress, item))) {
      walletList.push({ address: userMetadata.publicAddress.toLowerCase(), type: 'Magic' });
      localStorage.setItem('wallets', JSON.stringify(walletList));
    }
    dispatch(setWalletList(walletList));
    dispatch(setCurrentWallet(userMetadata.publicAddress.toLowerCase()))
    localStorage.setItem('currentWallet', userMetadata.publicAddress.toLowerCase())
  }

  return (
    <Grid container spacing={1}> 
        <Grid item xs={12} sm={12} md={12} className={'classes.paper'}>
          <Dialog onClose={handleClose} open={open} className={classes.dialog}>
            <div className={classes.root}>
              <div className={classes.container}>
                <Grid container spacing={2}>
                  <Grid item md={12}>
                    <Box className={classes.title}>
                      Enter your email to start using clutch
                    </Box>            
                  </Grid>
                  <Grid item md={9}>
                    <EmailInput onChange={(e) => setEmail(e.target.value)} placeholder={"Email"}/>
                  </Grid>
                  <Grid item md={3}>
                    <IconButton color="primary" aria-label="upload picture" component="span" className={classes.control_button}>
                      <GoIcon onClick={handleLoginWithEmail}/>
                    </IconButton>
                  </Grid>
                  <Grid item md={12} style={{marginTop: 32}}>
                    <Box className={classes.title}>
                      Or continue with
                    </Box>            
                  </Grid>
                  <Grid item md={3}>
                    <Button variant="contained" size="medium" className={classes.social}>
                      <FacebookIcon/>
                    </Button>
                  </Grid>
                  <Grid item md={3}>
                    <Button variant="contained" size="medium" className={classes.social} onClick={() => handleLoginWithSocial()}>
                      <GoogleIcon/>
                    </Button>
                  </Grid>
                  <Grid item md={3}>
                    <Button variant="contained" size="medium" className={classes.social}>
                      <AppleIcon/>
                    </Button>
                  </Grid>
                </Grid>
              </div>
            </div>
          </Dialog>
        </Grid>
      </Grid>
  );
};

export default ConnectMagicModal;
