import React from 'react';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles(theme => ({
  header: {
    fontSize: 12,
    fontWeight: 700,
    lineHeight: '14.4px',
    color: theme.palette.text_sub,
    fontStyle: 'italic'
  },
  description: {
    fontSize: 12,
    fontWeight: 400,
    lineHeight: '14.4px',
    color: theme.palette.text_sub,
    fontStyle: 'italic'
  },
  divider: {
    margin: theme.spacing(6, 0, 2, 0),
    height: '1px',
    backgroundColor: theme.palette.border_color
  },
  icons: {
    display: 'flex',
    marginTop: '16px',
  },
  icon: {
    width: '25px',
    marginRight: '15px'
  }
}));

const ContentFooter = props => {
  const classes = useStyles();

  return (
    <div>
      <div className={classes.divider} />
      <div className={classes.header}>
        Clutch Wallet
      </div>
      <div className={classes.description}>
        Â© 2022 All Rights Reserved
      </div>
      <div className={classes.icons}>
        <a href="https://discord.com/invite/tep7VRsPWb" target="_blank" rel="noopener noreferrer">
          <img src="/images/discord.png" alt="Discord" className={classes.icon}/>
        </a>
        <a href="https://twitter.com/clutch_wallet" target="_blank" rel="noopener noreferrer">
          <img src="/images/twitter.png" alt="Twitter" className={classes.icon}/>
        </a>
        <a href="https://www.linkedin.com/company/clutchwallet/about/" target="_blank" rel="noopener noreferrer">
          <img src="/images/linkedin.png" alt="Linkedin" className={classes.icon}/>
        </a>
        <a href="https://www.facebook.com/groups/1849412601905811" target="_blank" rel="noopener noreferrer">
          <img src="/images/facebook.png" alt="Facebook" className={classes.icon}/>
        </a>
      </div>
    </div>
  );
};


export default ContentFooter;
