import axios from 'axios';
import authHeader from './auth-header';
class Auth {
    login = (email, password, isSocial) => {
        return axios
            .post(`${process.env.REACT_APP_BACKEND_URL}/customers/login`, {
                email: email,
                password: password,
                isSocial: isSocial
            })
            .then(response => {
                if (response.data.status === 200) {
                    localStorage.setItem('token', response.data.result.token);
                }
                return response.data;
            }).catch(error => {
                return error;
            })
    }
    register = (email, password, isSocial) => {
        return axios
            .post(`${process.env.REACT_APP_BACKEND_URL}/customers/register`, {
                email: email,
                password: password,
                isSocial: isSocial
            })
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }
    forgot = (email) => {
        return axios
            .post(`${process.env.REACT_APP_BACKEND_URL}/customers/forgot`, {
                email: email
            })
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }

    verify = (token) => {
        return axios
            .post(`${process.env.REACT_APP_BACKEND_URL}/customers/verify`, {
                token: token
            })
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }

    resetPassword = (token, password) => {
        return axios
            .post(`${process.env.REACT_APP_BACKEND_URL}/customers/resetPassword`, {
                password: password,
                token: token
            })
            .then(response => {
                return response.data;
            }).catch(error => {
                return error;
            })
    }

    validateToken = () => {
        return axios
			.get(`${process.env.REACT_APP_BACKEND_URL}/customers/checkToken`, { headers: authHeader(localStorage.getItem('token')) })
			.then(response => {
				return response.data;
			}).catch(error => {
				return error;
			})
    }


}
export default new Auth();