import React, { useEffect, useRef, useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Box, Card } from '@material-ui/core';
import useWindowSize from 'shared/hooks/useWindowSize';

const useStyles = makeStyles(theme => ({
  root: {
    borderRadius: 12,
    minHeight: 340,
    border: `1px solid ${theme.palette.border_color}`,
    boxShadow: 'none',
    [theme.breakpoints.down('md')]: {
      height: 260,
      minHeight: 0
    }
  },
  imageContainer: {
    width: '100%',
    height: '191px',
    [theme.breakpoints.down('md')]: {
      height: 145
    }
  },
  image: {
    width: '100%',
    height: '100%',
    display: 'block',
    objectFit: 'cover'
  },
  textContainer: {
    padding: theme.spacing(2.5),
    [theme.breakpoints.down('md')]: {
      height: '115px',
      display: 'flex',
      alignItems: 'flex-start',
      flexDirection: 'column',
      justifyContent: 'center',
      padding: theme.spacing(1.75)
    }
  },
  textHeader: {
    fontSize: 18,
    color: theme.palette.text_main,
    marginBottom: theme.spacing(1),
    [theme.breakpoints.down('md')]: {
      fontSize: '16px'
    }
  },
  textContent: {
    fontSize: 14,
    lineHeight: '20px',
    color: theme.palette.text_sub,
    WebkitLineClamp: 3,
    overflow: 'hidden',
    textOverFlow: 'ellipsis'
  }
}));

const ImageCard = props => {
  const { children, key, title, image, text, href } = props;
  const classes = useStyles();
  const size = useWindowSize();

  return (
    <Card style={{ maxWidth: size.width }} className={classes.root} key={key}>
      <a href={href} target="_blank">
        <Box className={classes.imageContainer}>
          <img src={image} className={classes.image} />
        </Box>
        <Box className={classes.textContainer}>
          <Box className={classes.textHeader}>{title}</Box>
          <Box
            className={classes.textContent}
            dangerouslySetInnerHTML={{ __html: text }}
          />
        </Box>
      </a>
    </Card>
  );
};

export default ImageCard;
