import React, { useEffect, useRef, useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Box, Card } from '@material-ui/core';
import { DownIcon, UpIcon } from './icons/icons';

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    padding: theme.spacing(3, 3, 6, 3),
    boxShadow: 'none',
    border: `1px solid ${theme.palette.border_color}`,
    borderRadius: '24px',
    [theme.breakpoints.down('xs')]: {
      padding: 0
    }
  },
  root_expand_in: {},
  root_expand_out: {
    maxHeight: '300px',
    overflow: 'hidden'
  },
  pin: {
    fontSize: 16,
    fontWeight: 600,
    color: theme.palette.controller_color,
    display: 'flex',
    justifyContent: 'center',
    cursor: 'pointer',
    position: 'absolute',
    width: '100%',
    bottom: 0,
    padding: '14px',
    alignItems: 'center',
    boxShadow: `13px 13px 0px 15px ${theme.palette.topbar_background}`,
    background: theme.palette.topbar_background
  },
  blur_background: {
    width: 'calc(100% - 48px)',
    height: 72,
    position: 'absolute',
    bottom: 38,
    background: theme.palette.blur_background,
    transform: 'rotate(-180deg)'
  }
}));

const ExpandCard = props => {
  const { children } = props;
  const classes = useStyles();
  const [status, setStatus] = useState(false);
  const [height, setHeight] = useState(0);
  const childrenEl = useRef(null);

  useEffect(() => {
    setHeight(childrenEl?.current?.clientHeight || 0);
  }, [children]);
  return (
    <Card className={classes.root}>
      <div
        className={status ? classes.root_expand_in : classes.root_expand_out}>
        <div ref={childrenEl}>{children}</div>
      </div>
      {height > 300 && !status && <div className={classes.blur_background} />}
      {height > 300 && (
        <div className={classes.pin} onClick={() => setStatus(!status)}>
          {status ? 'Hide' : 'View all'}
          <Box style={{ marginLeft: '5px' }}>
            {!status ? <DownIcon /> : <UpIcon />}
          </Box>
        </div>
      )}
    </Card>
  );
};

export default ExpandCard;
