import { Box, Button, Dialog, IconButton, Grid } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { withStyles } from '@material-ui/styles';
import { getReducedWalletAddress } from 'utils/services';
import { CloseIcon } from './icons/icon';
import useStyles from './style';

const MuiDialog = withStyles((theme) => ({
  root: {
      backgroundColor: "rgba(63, 66, 113, 0.15)",
      margin: 0
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: `1px solid ${theme.palette.border_color}`,
    boxShadow: 'rgba(63, 66, 113, 0.15)',
    borderRadius: "16px",
    [theme.breakpoints.down('xs')]: {
      padding: "1.5rem 1rem !important"
    },
    [theme.breakpoints.up('sm')]: {
      padding: "1rem !important"
    },
    [theme.breakpoints.up('md')]: {
      padding: "2rem !important"
    }
  },
 
}))(Dialog);

const DeleteModal = props => {
  const { open, handleClose, selectedItem, handleDelete } = props;
  const classes = useStyles();

  return (
    <Grid container spacing={1}> 
      <Grid item xs={12} sm={12} md={12} className={'classes.paper'}> 
        <MuiDialog onClose={handleClose} open={open} className={classes.dialog}>
          <Box className={classes.header}>
            <Box>Delete Wallet</Box>
            <Box className={classes.icon} onClick={handleClose}>
              <IconButton
                color="primary"
                aria-label="upload picture"
                component="span"
                className={classes.control_button}>
                <CloseIcon />
              </IconButton>
            </Box>
          </Box>
          <Box className={classes.divider} />
          <Box className={classes.content}>
            You really want to delete
            <Box className={classes.bold}>{getReducedWalletAddress(selectedItem?.address || '')}</Box>
          </Box>
          <Button variant="contained" size="medium" className={classes.delete} onClick={() => {handleDelete(); handleClose();}}>
            Delete Wallet
          </Button>

          <Button size="medium" className={classes.cancel} onClick={handleClose}>
            Cancel
          </Button>
        </MuiDialog>
      </Grid>
    </Grid>
  );
};

export default DeleteModal;
