import { Box, Button, Dialog, IconButton, Popover, Card } from '@material-ui/core';
import { InfoIcon } from 'assets/svgs/icon';
import SlippageInput from 'components/SlippageInput';
import React, { useEffect, useState } from 'react';
import { CloseIcon } from './icons/icon';
import useStyles from './style';

const TransactionSettingsModal = props => {
  const { open, handleClose, slippage, speed, inputedSlippage, handleApply } = props;
  const classes = useStyles();
  const [_speed, _setSpeed] = useState(speed);
  const [_inputedSlippage, _setInputedSlippage] = useState(inputedSlippage);
  const [_slippage, _setSlippage] = useState(slippage);
  const [tooltip, setToolTip] = useState(0);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const tipOpen = Boolean(anchorEl);

  const handleClick = (event, mode) => {
    setAnchorEl(event.currentTarget);
    setToolTip(mode);
  };

  const handleCloseTip = () => {
    setAnchorEl(null);
  };

  return (
    <Dialog onClose={handleClose} open={open} className={classes.dialog}>
      <Box className={classes.header}>
        <Box>Transaction Settings</Box>
        <Box className={classes.icon} onClick={handleClose}>
          <IconButton
            color="primary"
            aria-label="upload picture"
            component="span"
            className={classes.control_button}>
            <CloseIcon />
          </IconButton>
        </Box>
      </Box>
      <Box className={classes.divider} />
      <Box className={classes.description}>
        Slippage tolerance
        <InfoIcon className={classes.icon} onClick={(e) => handleClick(e, 0)}/>
      </Box>
      <Box className={classes.button_group}>
        <Button variant="outlined" size="medium" className={_slippage == 0 ? classes.outlined_button_active:classes.outlined_button} onClick={() => _setSlippage(0)}>
          0.5%
        </Button>
        <Button variant="outlined" size="medium" className={_slippage == 1 ? classes.outlined_button_active:classes.outlined_button} onClick={() => _setSlippage(1)}>
          1%
        </Button>
        <Button variant="outlined" size="medium" className={_slippage == 2 ? classes.outlined_button_active:classes.outlined_button} onClick={() => _setSlippage(2)}>
          2%
        </Button>
        <SlippageInput
          placeholder="Type..."
          value={_inputedSlippage}
          onChange={(e) => _setInputedSlippage(e.target.value)}
        />
      </Box>
      
      <Box className={classes.speed_description}>
        Transaction speed
        <InfoIcon className={classes.icon} onClick={(e) => handleClick(e, 1)}/>
      </Box>
      <Box className={classes.button_group}>
        <Button variant="outlined" size="medium" className={_speed == 0 ? classes.outlined_button_active:classes.outlined_button} onClick={() => _setSpeed(0)}>
          Slow
        </Button>
        <Button variant="outlined" size="medium" className={_speed == 1 ? classes.outlined_button_active:classes.outlined_button} onClick={() => _setSpeed(1)}>
          Fast
        </Button>
        <Button variant="outlined" size="medium" className={_speed == 2 ? classes.outlined_button_active:classes.outlined_button} onClick={() => _setSpeed(2)}>
          Fastest
        </Button>
      </Box>
      <Box className={classes.speed_detail}>
        <Box>
          Estimated transaction time: 10 minutes
        </Box>
        <Box>
          Estimated network fee: $1.00
        </Box>
      </Box>
      <Button variant="contained" size="medium" className={classes.apply} onClick={() => {handleApply(_slippage, _speed, _inputedSlippage); handleClose();}}>
        Apply Settings
      </Button>
      <Popover
        className={classes.popover}
        open={tipOpen}
        anchorEl={anchorEl}
        onClose={handleCloseTip}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}>
        <Card className={classes.card}>
          {
            tooltip == 0 ?
              <>
                <Box className={classes.sub}>
                  Slippage is the difference between the expected price of an order and the price the order executes at. 
                </Box>
                <Box className={classes.sub} style={{marginTop: 20}}>
                  If the cryptocurrency pair moves by more than the amounts detailed below at the time you place your order, the transaction will fail to execute.
                </Box>
              </>
            :
              <>
                <Box className={classes.sub}>
                  Ethereum network fees (referred to as Gas) are charged by miners/validators that secure the blockchain network. Their fees vary depending on user demand.
                </Box>
                <Box className={classes.sub} style={{marginTop: 20}}>
                  Clutch does not profit from these network fees.
                </Box>
              </>
          }
        </Card>
      </Popover>
    </Dialog>
  );
};

export default TransactionSettingsModal;
