import React, { useState, useEffect, useMemo } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { Grid, Card, Box, Button } from '@material-ui/core';
import useStyles from './style';
import { NetworkSelector, TransactionSettingsModal, StepViewer, LoadingModal } from 'components';
import { TokenInput } from './components';
import { PreviewIcon, RevertIcon, SettingIcon } from './icons/icon';
import useActiveWeb3React from "hooks/web3-react"
import Web3 from 'web3';
import { approveToken, getBNValue, getCorrectDecValue, getErc20Balance, getLiquidityBalance, swapToken } from '../../web3/contracts';
import { ETH_ADDRESS, MAIN_NETWORK_ID, UNISWAP_ADDRESS, USDT_ADDRESS, WETH_ADDRESS } from 'utils/constants';
import { getAmountsOut } from 'web3/uniswap';
import { switchNetwork } from 'web3/network';
import { useToasts } from 'react-toast-notifications';
import { useSelector } from 'react-redux';

const getPriceImpact = (fromBalance, toBalance, _fromToken, _toToken) => {
  let toTokenRemainAmount = (_toToken.amount - (fromBalance * toBalance / (Number(fromBalance) + Number(_fromToken?.amount) || 0)))
  let rate = _fromToken.amount / toTokenRemainAmount
  return (rate / (fromBalance / toBalance) - 1).toFixed(3);
}

const Swap = props => {
  const theme = useTheme();
  const classes = useStyles(theme);
  const networkList = [
    { networkName: 'Ethereum', image: '/images/EthereumLogo.png' },
    { networkName: 'Polygon', image: '/images/Polygon.png' },
    { networkName: 'Arbitrum', image: '/images/ArbitrumLogo.png' }
  ];
  const [selectedNetwork, setSelectedNetwork] = useState({
    networkName: 'Ethereum',
    image: '/images/EthereumLogo.png'
  });
  const [fromToken, setFromToken] = useState({});
  const [settingsModalOpen, setSettingsModalOpen] = useState(false);
  const [toToken, setToToken] = useState({});
  const [slippage, setSlippage] = useState(0);
  const [speed, setSpeed] = useState(0);
  const [inputedSlippage, setInputedSlippage] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [hash, setHash] = useState('');
  const [outputMin, setOutputMin] = useState(0);
  const [approveButton, setApproveButton] = useState(false);
  const [totalFromBalance, setTotalFromBalance] = useState(0);
  const [totalToBalance, setTotalToBalance] = useState(0);
  const priceImpact = useMemo(() => getPriceImpact(totalFromBalance,  totalToBalance, fromToken, toToken), [totalFromBalance,  totalToBalance, fromToken, toToken]);
  const { account, library, chainId } = useActiveWeb3React();
  const currentWallet = useSelector(state => state.app.currentWallet);
  const { addToast } = useToasts();

  useEffect(() => {
    let _slippage = 0;
    if (inputedSlippage > 0) {
      _slippage = Number(inputedSlippage)
    } else if (slippage == 0) {
      _slippage = 0.5;
    } else if (slippage == 1) {
      _slippage = 1;
    } else if (slippage == 2) {
      _slippage = 2;
    }
    setOutputMin((100 - _slippage) * (getBNValue(toToken.amount || 0, toToken.decimals)))
  }, [slippage, inputedSlippage, toToken])

  useEffect(() => {
    if (fromToken.address && fromToken.address != ETH_ADDRESS)
      setApproveButton(true)
    else
      setApproveButton(false)
  }, [fromToken])

  useEffect(() => {
    const getTotalBalance = async () => {
      const ret = await getLiquidityBalance(fromToken.address, toToken.address)
      if (ret.status) {
        setTotalFromBalance(getCorrectDecValue(ret.data.from, fromToken.decimals, -1));
        setTotalToBalance(getCorrectDecValue(ret.data.to, toToken.decimals, -1));
      }
    }
    if (fromToken.symbol && toToken.symbol) {
      getTotalBalance()
    }
  }, [fromToken, toToken])

  const getAmountOut = async (_fromToken, _toToken) => {
    let fromAddress = (_fromToken?.address == ETH_ADDRESS ? WETH_ADDRESS : _fromToken?.address);
    let toAddress = (_toToken?.address == ETH_ADDRESS ? WETH_ADDRESS : _toToken?.address);
    let amount = 0;
    const result = await getAmountsOut(UNISWAP_ADDRESS, [fromAddress, toAddress], getBNValue(_fromToken?.amount, _fromToken.decimals) )
    if (result.status) {
      amount = getCorrectDecValue(result.data[1], _toToken.decimals, -1)
    }
    return amount;
  }

  const handleChangeToken = async (item, isFrom) => {
    if (isFrom) {
      setFromToken(item)
    } else {
      setToToken(item)
    }
  }

  const handleChangeAmount = async (item) => {
    setFromToken(item)
    if (fromToken?.symbol && toToken?. symbol) {
      let _toToken = JSON.parse(JSON.stringify(toToken));
      if (!isNaN(item.amount) && Number(item.amount) != 0) {
        _toToken.amount = await getAmountOut(item, toToken);
      } else {
        _toToken.amount = 0;
      }
      setToToken(_toToken)
    }
  }

  const exchangeToken = () => {
    let _fromToken = JSON.parse(JSON.stringify(fromToken));
    let _toToken = JSON.parse(JSON.stringify(toToken));
    setFromToken(_toToken);
    setToToken(_fromToken);
  }

  const handleApply = (_slippage, _speed, _inputedSlippage) => {
    setSlippage(_slippage);
    setSpeed(_speed);
    setInputedSlippage(_inputedSlippage)
  }

  const previewSwap = async () => {
    if (!currentWallet || currentWallet.length == 0)
      addToast('Please connect wallet.', { appearance: 'error', autoDismiss: true});
    else if (currentWallet != account.toLowerCase())
      addToast(`Please switch your account to ${currentWallet}`, { appearance: 'error', autoDismiss: true});
    else if (!fromToken?.symbol || !toToken?.symbol || !fromToken?.amount || fromToken?.amount == 0 || fromToken?.amount >= getCorrectDecValue(fromToken?.balance || 0, fromToken?.decimals, -1))
      addToast('Invalid token, amount', { appearance: 'error', autoDismiss: true });
    else if (library) {
      setLoading(true);
      setError(false);
      const net_ret = await switchNetwork(MAIN_NETWORK_ID)
      if (net_ret) {
        const web3 = new Web3(library.provider)
        const result = await swapToken(web3, fromToken?.address, toToken?.address, currentWallet, getBNValue(fromToken?.amount, fromToken?.decimals), getBNValue(toToken?.amount, toToken?.decimals), outputMin)
        if (result.status) {
          setError(false);
          setHash(result.data.hash)
        } else {
          setError(true);
        }
      } else {
        setError(true);
      }

      setLoading(false);
    }
  }

  const handleApprove = async () => {
    if (!currentWallet || currentWallet.length == 0)
      addToast('Please connect wallet.', { appearance: 'error', autoDismiss: true});
    else if (currentWallet != account.toLowerCase())
      addToast(`Please switch your account to ${currentWallet}`, { appearance: 'error', autoDismiss: true});
    else if (!fromToken?.symbol || !toToken?.symbol || !fromToken?.amount || fromToken?.amount == 0 || fromToken?.amount >= getCorrectDecValue(fromToken?.balance || 0, fromToken?.decimals, -1))
      addToast('Invalid token, amount', { appearance: 'error', autoDismiss: true });
    else if (library) {
      setLoading(true);
      setError(false);
      const net_ret = await switchNetwork(MAIN_NETWORK_ID)
      if (net_ret) {
        const web3 = new Web3(library.provider)
        const result = await approveToken(web3, fromToken?.address, getBNValue(fromToken?.amount, fromToken?.decimals), account)
        if (result.status) {
          setError(false);
          setHash(result.data.hash)
          setApproveButton(false);
        } else {
          setError(true);
          addToast('Approve failed', { appearance: 'error', autoDismiss: true });
        }
      } else {
        setError(true);
        addToast('Switch network failed', { appearance: 'error', autoDismiss: true });
      }

      setLoading(false);
    }
  }

  return (
    <Box className={classes.root}>
      <Grid container spacing={7}>
        <Grid item xs="auto">
          <StepViewer step="STEP 1:" description="Select your network" />
          <NetworkSelector
            list={networkList}
            selectedItem={selectedNetwork}
            setSelectedItem={setSelectedNetwork}
          />
        </Grid>
        <Grid item md={6} sm={12}>
          <Card className={classes.step_card}>
            <Box className={classes.card_header}>
              <StepViewer step="STEP 2:" description="Swap" />
              <Box className={classes.settings_container}>
                <Box className={classes.setting_description}>
                  Transaction Settings:
                </Box>
                <SettingIcon className={classes.setting_icon} onClick={() => setSettingsModalOpen(true)}/>
              </Box>
            </Box>
            <Box style={{position: "relative"}}>
              <TokenInput tokenInfo={fromToken} isFrom={true} handleChange={(token) => handleChangeToken(token, true)} handleChangeAmount={(token) => handleChangeAmount(token)}/>
              <RevertIcon className={classes.revertIcon} onClick={exchangeToken}/>
              <Box style={{marginTop: '6px'}} />
              <TokenInput tokenInfo={toToken} isFrom={false} handleChange={(token) => handleChangeToken(token, false)}/>
            </Box>
            <Box className={classes.rate_box}>
              <Box className={classes.main}>
                Rate:
              </Box>
              <Box>
                {
                  fromToken?.symbol && toToken?.symbol 
                  ?
                  <Box className={classes.main}>
                    {`${fromToken?.amount ? 1 : 0} ${fromToken.symbol} = ${((toToken?.amount || 0) / (fromToken?.amount || 1)).toFixed(3)} ${toToken.symbol}`}
                    <Box className={classes.sub}>
                      {`($ ${fromToken?.amount ? (fromToken?.price || 0).toFixed(3) : 0})`}
                    </Box>
                  </Box>
                  :
                  <Box className={classes.main}>
                    0
                    <Box className={classes.sub}>
                      ($ 0)
                    </Box>
                  </Box>
                }
              </Box>
            </Box>
            <Box className={classes.impact_box}>
              <Box style={{display: 'flex', justifyContent: 'space-between'}}>
                <Box className={classes.main}>
                  Expected Output
                </Box>
                <Box className={classes.main}>
                  {
                    toToken.symbol ?
                      `${Number(toToken?.amount || 0).toFixed(3)} ${toToken.symbol}`
                    :
                      '-'
                  }
                </Box>
              </Box>
              <Box style={{display: 'flex', justifyContent: 'space-between', marginTop: 6}}>
                <Box className={classes.main}>
                  Price Impact
                </Box>
                <Box className={classes.main}>
                  {
                    (toToken?.amount || 0) * (toToken?.price || 0)  == 0 ?
                    '0%'
                    :
                    `${priceImpact}%`
                  }
                </Box>
              </Box>
            </Box>
            <Button 
              variant="contained"
              className={classes.preview_button}
              onClick={() => {approveButton ? handleApprove() : previewSwap()}}>
                {
                  approveButton ? 'Approve' : 'Preview Swap'
                }
                <PreviewIcon className={classes.preview_icon}/>
            </Button>
          </Card>
        </Grid>
        <Grid item md={3} xs={12}></Grid>
      </Grid>
      <TransactionSettingsModal 
        open={settingsModalOpen}
        handleClose={() => setSettingsModalOpen(false)}
        slippage={slippage}
        speed={speed}
        inputedSlippage={inputedSlippage}
        handleApply={handleApply}
      />
      <LoadingModal
        open={error || loading}
        handleClose={() => error ? setError(false): setLoading(false)}
        handleApply={() => error ? (approveButton ? handleApprove() : previewSwap()) : setLoading(false)}
        isLoading={loading}
      />
    </Box>
  );
};

export default Swap;
