import React, { useEffect, useState } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { Grid, Box } from '@material-ui/core';
import loginFrame from './icons/loginFrame.png'

const useStyles = makeStyles(theme => ({
  main_logo: {
    height: 40
  },
  description: {
    fontSize: 52,
    color: 'white',
    lineHeight: '60px',
    fontWeight: 500,
    maxWidth: 382,
    marginTop: 64
  }, 
  loginFrameImg: {
    width: '100%',
    height: '100vh !important',
    [theme.breakpoints.down('sm')]: {
      height: '60vh !important'
    },
  },
  container: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    '& .MuiBox-root-8': {
      minHeight: 'auto !important',
    },
    [theme.breakpoints.down('sm')]: {
      margin: "1.5rem 1rem",
    },
    [theme.breakpoints.down('xs')]: {
      "& $loginFormCard": {
        width: "100%",
      }
    },
  },
}));

const Auth = props => {
  const { children } = props;

  const classes = useStyles();

  return (
    <Grid container>
      <Grid item xs={12} sm={12} md={6}>
        <Box style={{padding: 40, backgroundColor: 'blue', height: '100vh'}}>
          <img src="/images/Logo.svg" className={classes.main_logo}/>
          <Box className={classes.description}>
            Let's get you set up with a Clutch Wallet profile!
          </Box>
        </Box>
        {/* <Box
            component="img"
            alt="Login Frame"
            src={loginFrame}
            className={classes.loginFrameImg}
          /> */}
      </Grid>
      <Grid item xs={12} sm={12} md={6} className={classes.container}>
        {children}
      </Grid>
    </Grid>
  );
};

export default Auth;
