import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles(theme => ({
  root: {
    boxShadow: 'none',
    left: '279px',
    width: 'calc(100% - 279px)',
    height: '72px',
    backgroundColor: theme.palette.topbar_background,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: '101',
    borderBottom: `1px solid ${theme.palette.border_color}`,
  },
  leftContainer: {
    display: 'flex'
  },
  flexGrow: {
    flexGrow: 1
  },
  signOutButton: {
    marginLeft: theme.spacing(1)
  },
  searchbar: {
    [theme.breakpoints.down('xs')]: {
      display: 'none'
    },
    [theme.breakpoints.up('xs')]: {
      width: 300,
      paddingLeft: theme.spacing(4)
    },
    [theme.breakpoints.up('sm')]: {
      width: 420,
      paddingLeft: theme.spacing(6)
    }
  },
  searchIcon: {
    display: 'none',
    [theme.breakpoints.down('xs')]: {
      display: 'flex',
      alignItems: 'center',
      width: '30px'
    }
  },
  toolbar: {
    display: 'flex',
    width: '100%',
    padding: '0px 10px',
    justifyContent: 'space-between'
  },
  close_drawer_icon: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    width: 60,
    '& .MuiSvgIcon-root': {
      fontSize: '1.875em'
    }
  },
  title: {
    color: theme.palette.gray,
    fontSize: '1.25em',
    fontWeight: '400',
    display: 'flex',
    alignItems: 'center',
    marginLeft: '16px'
  },
  avatar: {
    display: 'flex',
    alignItems: 'center'
  },
  rightControllerArea: {
    display: 'flex',
    marginRight: theme.spacing(5)
  },
  titlebar: {
    display: 'flex',
    borderRight: `1px solid ${theme.palette.border_color}`,
    marginTop: '-13px',
    marginBottom: '-13px',
    [theme.breakpoints.up('md')]: {
      display: 'none'
    }
  },
  avataricon: {
    fontSize: '2.5em',
    color: theme.palette.text.primary
  },
  helper: {
    margin: theme.spacing(0, 3),
    display: 'flex',
    alignItems: 'center',
    cursor: 'pointer'
  },
  vertical_separator: {
    height: '60%',
    width: '1px',
    backgroundColor: theme.palette.select_title,
    marginTop: '7%'
  },
  connect_button: {
    backgroundColor: theme.palette.wallet_button_color,
    color: theme.palette.text_main,
    borderRadius: '8px',
    boxShadow: 'none',
    textTransform: 'none',
    alignItems: 'center',
    '&:hover': {
      boxShadow: 'none',
      backgroundColor: theme.palette.wallet_button_color,
      color: theme.palette.text_main
    }
  },
  eth_icon: {
    marginRight: theme.spacing(1)
  },
  card: {
    padding: theme.spacing(4),
    // borderRadius: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      width: 450,
    },
    [theme.breakpoints.down('xs')]: {
      padding: "1.5rem .8rem !important",
      width: "330px",
    },
  },
  popover: {
    '& .MuiPopover-paper': {
      marginTop: 6,
      borderRadius: 24,
      boxShadow: 'none',
      border: `1px solid ${theme.palette.border_color}`
    },
    [theme.breakpoints.down('xs')]: {
      left: "1rem !important",
      
    },
  },
}));

export default useStyles;
