import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { Input } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';

const useStyles = makeStyles(theme => ({
  root: {
    color: '#8087ED',
    border: `1px solid #B2B7FF`,
    borderRadius: 8,
    display: 'flex',
    alignItems: 'center',
    maxWidth: 120
  },
  icon: {
    marginRight: theme.spacing(1),
    marginLeft: theme.spacing(1),
    color: '#8087ED',
  },
  input: {
    flexGrow: 1,
    fontSize: 16,
    lineHeight: '16px',
    letterSpacing: '-0.05px',
    color: '#8087ED',
    padding: theme.spacing(1, 2),
    height: 50,
  },
  error_root: {
    color: '#8087ED',
    border: `1px solid #FF8585`,
    borderRadius: 8,
    display: 'flex',
    alignItems: 'center',
    maxWidth: 120
  }
}));

const SlippageInput = props => {
  const { className, onChange, style, placeholder, value, ...rest } = props;

  const classes = useStyles();

  return (
    <div>
      <div className={isNaN(value) || value > 100 || value < 0 ? classes.error_root :classes.root}>
        <Input
          {...rest}
          className={classes.input}
          disableUnderline
          onChange={onChange}
          placeholder={placeholder}
          value={value}
        />
        <div className={classes.icon}>
          %
        </div>
      </div>
      {
        (isNaN(value) || value > 100 || value < 0) &&
        <div style={{color: '#FF8585'}}>
          Error caption
        </div>
      }
    </div>
  );
};

SlippageInput.propTypes = {
  className: PropTypes.string,
  onChange: PropTypes.func,
  style: PropTypes.object
};

export default SlippageInput;
