import React, { useEffect, useRef, useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Card, Popover, Box, Button } from '@material-ui/core';
import { KeyboardArrowDown, KeyboardArrowUp } from '@material-ui/icons';

const useStyles = makeStyles(theme => ({
  root: {
    marginTop: theme.spacing(2),
  },
  connect_button: {
    padding: 0,
    boxShadow: 'none',
    borderRadius: 9,
    border: `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.wallet_button_color,
    textTransform: 'none',
    '&:hover': {
      backgroundColor: theme.palette.wallet_button_color,
      boxShadow: 'none',
    }
  },
  image: {
    width: 40,
    height: 40,
    marginRight: 10,
  },
  title: {
    marginRight: 15.5,
    width: 77
  },
  icon: {
    marginRight: 16.5,
    display: 'flex',
    alignItems: 'center'
  },
  card: {
    padding: theme.spacing(1.5),
    backgroundColor: theme.palette.wallet_button_color,
  },
  item: {
    display: 'flex',
    alignItems: 'center',
    cursor: 'pointer',
    color: theme.palette.text_main
  },
  divider: {
    margin: theme.spacing(1.5, 0),
    height: '1px',
    backgroundColor: theme.palette.border_color
  },
  popover: {
    '& .MuiPopover-paper': {
      marginTop: 6,
      borderRadius: 9,
      boxShadow: 'none',
      border: `1px solid ${theme.palette.border_color}`,
    }
  }
}));

const NetworkSelector = props => {
  const { children, selectedItem, setSelectedItem, list } = props;
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [targetWidth, setTargetWidth] = useState(0);
  const open = Boolean(anchorEl);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
    setTargetWidth(event.currentTarget.offsetWidth);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const listFilter = (item) => {
    if (item.networkName === selectedItem?.networkName)
      return false;
    return true;
  }

  return (
    <Box className={classes.root}>
      <Button
        variant="contained"
        className={classes.connect_button}
        onClick={handleClick}>
        <img src={selectedItem?.image} className={classes.image}/>
        <Box className={classes.title}>
          {selectedItem?.networkName}
        </Box>
        <Box className={classes.icon}>
          {!open ? <KeyboardArrowDown /> : <KeyboardArrowUp />}
        </Box>
      </Button>
      <Popover
        className={classes.popover}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right'
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}>
        <Card className={classes.card} style={{width: targetWidth}}>
          {
            (list.filter(listFilter) || []).map((item, index) => (
              index == 0 ?
              <Box key={index} className={classes.item} onClick={() => {handleClose(); setSelectedItem(JSON.parse(JSON.stringify(item)))}}>
                <img src={item.image} className={classes.image}/>
                {item.networkName}
              </Box>
              :
              <Box key={index} onClick={() => {handleClose(); setSelectedItem(JSON.parse(JSON.stringify(item)))}}>
                <Box className={classes.divider}/>
                <Box className={classes.item}>
                  <img src={item.image} className={classes.image}/>
                  {item.networkName}
                </Box>
              </Box>
            ))
          }
        </Card>
      </Popover>
    </Box>
  );
};

export default NetworkSelector;
