import React, { useEffect, useRef, useState } from 'react';
import { SiteInfoContextConsumer } from 'App';

import { Box, Tooltip } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import useStyles from './style';
import { useHistory } from 'react-router-dom';
import { CopyIcon, EllipseIcon, EthLogoIcon, QRIcon } from './svgs/icons';
import { setCurrentWallet } from 'store/actions/App';
import { useDispatch, useSelector } from 'react-redux';
import { getReducedWalletAddress } from 'utils/services';
import { ConnectWalletModal, QRModal } from 'components';

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: theme.palette.topbar_background,
    border:  `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    boxShadow: theme.shadows[1],
    fontSize: 11,
  },
  arrow: {
    color: theme.palette.topbar_background,
  }
}))(Tooltip);

const WalletCard = props => {
  const {
    handleClose,
    setWalletModalOpen,
    walletModalOpen,
  } = props;
  const dispatch = useDispatch();
  const history = useHistory();
  const walletList = useSelector(state => state.app.walletList);
  const currentWallet = useSelector(state => state.app.currentWallet);
  const [copied, setCopied] = useState(false);
  const [qrModalOpen, setQrModalOpen] = useState(false);

  const classes = useStyles();

  useEffect(() => {
    if (copied) {
      if ("clipboard" in navigator) {
        navigator.clipboard.writeText(currentWallet);
      } else {
        document.execCommand("copy", true, currentWallet);
      }
      setTimeout(() => {
        setCopied(false)
      }, 2000)
    }
  }, [copied])
  return (
    <Box className={classes.root}>
      <Box className={classes.wallets}>
        MY WALLETS:
      </Box>
      {
        walletList.map((item, index) => (
          <Box className={item.address.toLowerCase() == (currentWallet || '').toLowerCase() ?
            classes.active_control : classes.control} onClick={() => {dispatch(setCurrentWallet(item.address.toLowerCase())); localStorage.setItem('currentWallet', item.address.toLowerCase())}}
            style={{width: "100%"}}>
            <Box className={classes.left}>
              <EthLogoIcon className={classes.eth_icon} />
              <Box>
                <Box className={classes.name}>
                  {getReducedWalletAddress(item.address)}
                </Box>
                <Box className={classes.description}>
                  $0
                  <EllipseIcon style={{marginLeft: 6, marginRight: 6}}/>
                  {item.type}
                </Box>
              </Box>
            </Box>
            <Box style={{display: 'flex', alignItems: 'center'}}>
              <LightTooltip title={copied ? "Copied!" : "Copy to Clipboard"} arrow>
                <CopyIcon className={classes.icon} style={{marginRight: 8}} onClick={() => setCopied(true)}/>
              </LightTooltip>
              <QRIcon className={classes.icon} onClick={() => setQrModalOpen(true)}/>
            </Box>
          </Box>
        ))
      }
      <Box className={classes.divider} />
      <Box className={classes.connect_wallet} onClick={() => {setWalletModalOpen(true); handleClose();}}>
        Connect another wallet
      </Box>
      <Box className={classes.manage_wallet} onClick={() => history.push('/wallets')}>
        Manage wallets
      </Box>
      <QRModal
        open={qrModalOpen}
        handleClose={() => setQrModalOpen(false)}
        value={currentWallet}
      />
    </Box>
  );
};

export default WalletCard;
