import React, { useState } from 'react';
import { useWeb3React } from '@web3-react/core';

import Web3 from 'web3';
import { useDispatch, useSelector } from 'react-redux';
import { magic } from 'utils/connectors';
import { setCurrentWallet, setWalletList } from 'store/actions/App';

const checkWallet = (account, wallet) => {
    return wallet.address === account.toLowerCase();
};

const useActiveWeb3React = () => {
    const currentWallet = useSelector(state => state.app.currentWallet);
    const walletList = useSelector(state => state.app.walletList);
    const { activate, deactivate, account, library, chainId } = useWeb3React();
    const dispatch = useDispatch()
    // const [_account, setAccount] = useState(null);
    // const [_library, setLibrary] = useState(null);
    // const [_activate, setActivate] = useState(null);

    const handleLoginWithEmail = async (email) => {
        let didToken = await magic.auth.loginWithMagicLink({
          email,
          // redirectURI: new URL('/callback', window.location.origin).href, // optional redirect back to your app after magic link is clicked
        });
        console.log(didToken);
        let userMetadata = await magic.user.getMetadata();
        console.log(userMetadata)
    
        let walletList = JSON.parse(localStorage.getItem('wallets') || '[]');
    
        if (!walletList.find((item) => checkWallet(userMetadata.publicAddress, item))) {
          walletList.push({ address: userMetadata.publicAddress.toLowerCase(), type: 'Magic' });
          localStorage.setItem('wallets', JSON.stringify(walletList));
        }
        dispatch(setWalletList(walletList));
        dispatch(setCurrentWallet(userMetadata.publicAddress.toLowerCase()))
        localStorage.setItem('currentWallet', userMetadata.publicAddress.toLowerCase())
    }

    let _account = account;
    let _library = library;
    let _activate = activate;

    let index = walletList.findIndex(({address}) => address === currentWallet);
    if (index >= 0)
        if (walletList[index].type == 'Magic') {
            _account = currentWallet;
            _library = {provider: magic.rpcProvider};
            _activate = handleLoginWithEmail;
        }
    return {activate: _activate, deactivate, account: _account, library: _library, chainId}
}

export default useActiveWeb3React