import apiFetchService from './APIFetchService';
const BASE_URL = 'https://api.ethplorer.io';
const apiService = {};

apiService.fetchTrendingCrypto = async () => {
  return await apiFetchService.get(
    `${process.env.REACT_APP_TRENDING_CRYPTO_API_URL}?vs_currency=usd&page=1&per_page=5&order=market_cap_desc`,
    {
      'x-rapidapi-host': `${process.env.REACT_APP_TRENDING_CRYPTO_API_HOST}`,
      'x-rapidapi-key': `${process.env.REACT_APP_COIN_DESK_API_KEY}`
    }
  );
};

export default apiService;
