import { Grid, Box } from '@material-ui/core';
import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { CheckIcon, LinkIcon } from './icons/icons';
import useStyles from './style';

const Wallet = props => {
  const {title, src, image_type, onClick} = props;

  const walletList = useSelector(state => state.app.walletList);
  const classes = useStyles();

  const isConnected = () => {
    let res = false;
    for (let i = 0; i <walletList.length; i ++) {
      if (walletList[i].type === title)
        res = true;
    }
    return res;
  }

  return (
    <Grid item xs={12} sm={6} >
      <Box className={classes.container} onClick={onClick}>
        <Box className={classes.left}>
          <img src={src} className={classes.icon}></img>
          <div className={classes.title}>{title}</div>
        </Box>
        <Box className={classes.right}>
          {isConnected() ?
            <Box className={classes.connectedStatus}>
              Connected
              <CheckIcon style={{marginLeft: 13}}/>
            </Box>
            :
            <Box className={classes.unconnectedStatus}>
              Connect
              <LinkIcon style={{marginLeft: 13}}/>
            </Box>
          }
        </Box>
      </Box>
    </Grid>
  );
};

export default Wallet;
