import { Box, Button, Dialog, IconButton, Popover, Card } from '@material-ui/core';
import { InfoIcon } from 'assets/svgs/icon';
import SlippageInput from 'components/SlippageInput';
import React, { useEffect, useState } from 'react';
import { CloseIcon } from './icons/icon';
import useStyles from './style';

const LoadingModal = props => {
  const { open, handleClose, isLoading, handleApply } = props;
  const classes = useStyles();

  return (
    <Dialog onClose={handleClose} open={open} className={classes.dialog}>
      <Box className={classes.header}>
        <Box>{isLoading ? 'Loading Preview' : 'Something went wrong..'}</Box>
        <Box className={classes.icon} onClick={handleClose}>
          <IconButton
            color="primary"
            aria-label="upload picture"
            component="span"
            className={classes.control_button}>
            <CloseIcon />
          </IconButton>
        </Box>
      </Box>
      <Box className={classes.divider} />
      
      <Button variant="contained" size="medium" className={classes.apply} onClick={() => {handleApply()}}>
        {isLoading ? 'Close' : 'Try Again'}
      </Button>
    </Dialog>
  );
};

export default LoadingModal;
