import Web3 from 'web3';
import erc20Abi from '../config/abi/erc20Abi.json';
import factoryAbi from '../config/abi/factoryAbi.json';
import { WETH_ADDRESS, DAI_ADDRESS } from "./constants";
const ethHttpProvider = new Web3.providers.HttpProvider(process.env.REACT_APP_NODE_URL);
const ethWeb3 = new Web3(ethHttpProvider);

const getETHPriceInfo = async (input, decimals) => {
    const uniV2 = "0x7a250d5630B4cF539739dF2C5dAcb4c659F2488D";
    const routerABI = [
        {
            inputs: [
                { internalType: "uint256", name: "amountIn", type: "uint256" },
                { internalType: "address[]", name: "path", type: "address[]" },
            ],
            name: "getAmountsOut",
            outputs: [
                { internalType: "uint256[]", name: "amounts", type: "uint256[]" },
            ],
            stateMutability: "view",
            type: "function",
        },
    ]

    return new Promise(async (resolve) => {
        let routerInstance = new ethWeb3.eth.Contract(routerABI, uniV2);
        let path = [input, DAI_ADDRESS];
        const pairAddress = await getUniPairAddress(input, DAI_ADDRESS);
        if (pairAddress == null) {
            const ethPair = await getUniPairAddress(input, WETH_ADDRESS);
            if (ethPair == null) {
                resolve(["0", "0"]);
            } else {
                let tokenInstance = new ethWeb3.eth.Contract(erc20Abi, WETH_ADDRESS);
                const balance = await tokenInstance.methods.balanceOf(ethPair).call();
                const flag =
                    parseInt(balance) < parseInt(ethWeb3.utils.toWei("10", "ether"));
                if (flag) {
                    resolve(["0", "0"]);
                }
                path = [input, WETH_ADDRESS, DAI_ADDRESS];
                routerInstance.methods
                    .getAmountsOut(ethWeb3.utils.toBN(1 * Math.pow(10, decimals)), path)
                    .call()
                    .then((data) => resolve(data))
                    .catch((e) => resolve(["0", "0"]));
            }
        } else {
            routerInstance.methods
                .getAmountsOut(ethWeb3.utils.toBN(1 * Math.pow(10, decimals)), path)
                .call()
                .then((data) => {
                    resolve(data)
                })
                .catch((e) => resolve(["0", "0"]));
        }
    });
}

const getUniPairAddress = async (quoteToken, baseToken) => {
    const ZERO_ADDRESS = "0x0000000000000000000000000000000000000000";
    const UNI_FACTORY_ADDRESS = "0x5C69bEe701ef814a2B6a3EDD4B1652CB9cc5aA6f";
    const uniFactoryContract = new ethWeb3.eth.Contract(
        factoryAbi,
        UNI_FACTORY_ADDRESS
    );

    const pairAddress = await uniFactoryContract.methods
        .getPair(quoteToken, baseToken)
        .call();
    if (pairAddress === ZERO_ADDRESS) {
        return null;
    }
    return pairAddress;
}

export const getPrice = async (tokenAddr) => {
    try {
        const erc20ABI = [
            {
                inputs: [],
                name: "decimals",
                outputs: [
                    {
                        internalType: "uint8",
                        name: "",
                        type: "uint8",
                    },
                ],
                stateMutability: "view",
                type: "function",
            },
        ];

        let tokenInstance, tokenDecimals, data, price;
        tokenInstance = new ethWeb3.eth.Contract(erc20ABI, tokenAddr);
        tokenDecimals = await tokenInstance.methods.decimals().call();
        data = await getETHPriceInfo(tokenAddr, tokenDecimals);
        price = parseFloat(ethWeb3.utils.fromWei(data[data.length - 1]));
        return price;
    } catch (error) {
        return 0;
    }
}
