export const getReducedWalletAddress = address => {
  if (address && address.length == 42) {
    return address.slice(0, 6) + '...' + address.slice(38, 42);
  }
};

export const RandomImages = () => {
  const images = [
    {
      image: '/images/EducationPic1.png'
    },
    {
      image: '/images/EducationPic2.png'
    }
  ];
  const randomImageIndex = Math.floor(Math.random() * (1 + 1));
  return images[randomImageIndex].image;
};
