const LightTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: theme.palette.topbar_background,
    border:  `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    boxShadow: theme.shadows[1],
    fontSize: 11,
  },
  arrow: {
    color: theme.palette.border_color,
  }
}))(Tooltip);

export default function WalletTableCard(props) {
  const theme = useTheme();
  const classes = useStyles();
  const dispatch = useDispatch();
  const [direction, setDirection] = useState(props.columns);
  const [copied, setCopied] = useState(null);
  const [deleteModalOpen, setDeleteModalOpen] = useState(null);
  const walletList = useSelector(state => state.app.walletList);
  const currentWallet = useSelector(state => state.app.currentWallet);
  const [selectedItem, setSelectedItem] = useState(null);
  const [toggleMenu, setToggleMenu] = useState(true);

  const max375 = useMediaQuery('(max-width:375px)');

  const [Item, setItem] = useState({
    x: 0,
    y: 0,
  });

  const tempDirection = props.columns;
  let tempDirect = [];
  if (tempDirection) {
    for (let i = 0; i < tempDirection.length; i++)
      tempDirect[i] = '/images/sort_down.png';
  }
  const [cells] = useState(props.cells);

  const items = props.products;
  const [direct, setDirect] = React.useState(tempDirect);

  useEffect(() => {
    if (copied) {
      if ("clipboard" in navigator) {
        navigator.clipboard.writeText(copied);
      } else {
        document.execCommand("copy", true, copied);
      }
      setTimeout(() => {
        setCopied(null)
      }, 2000)
    }
  }, [copied])

  const Sort = (index = 0) => {
    if (direction[index] === "ASC") {
      tempDirection[index] = "DESC";
      tempDirect[index] = <UpIcon />;
      setDirect(tempDirect);
      setDirection(tempDirection);
    } else {
      tempDirection[index] = "ASC";
      tempDirect[index] = <DownIcon />;
      setDirect(tempDirect);
      setDirection(tempDirection);
    }

    props.onSelectSort(index, tempDirection[index]);
  }

  const findWallet = (item) => {
    let wallet = WALLETS.find(({title}) => title === item.type);
    return wallet;
  }

  const handleDelete = (wallet_address) => {
    let index = walletList.findIndex(({address}) => address === wallet_address);
    if (index >= 0) {

      if (wallet_address === currentWallet.toLowerCase()) {
        if (walletList.length === 1) {
          dispatch(setCurrentWallet(''));
          dispatch(setWalletList([]));
          localStorage.setItem('currentWallet', '')
          localStorage.setItem('wallets', '[]')
        } else {
          let _walletList = JSON.parse(JSON.stringify(walletList));
          _walletList.splice(index, 1);
          dispatch(setCurrentWallet(_walletList[0].address.toLowerCase()))
          dispatch(setWalletList(_walletList))
          localStorage.setItem('currentWallet', _walletList[0].address.toLowerCase())
          localStorage.setItem('wallets', JSON.stringify(_walletList))
        }
      } else {
          let _walletList = JSON.parse(JSON.stringify(walletList));
          _walletList.splice(index, 1);

          dispatch(setCurrentWallet(_walletList[0].address.toLowerCase()))
          dispatch(setWalletList(_walletList))
          localStorage.setItem('currentWallet', _walletList[0].address.toLowerCase())
          localStorage.setItem('wallets', JSON.stringify(_walletList))
      }
    }
  }

  return (
    <Grid container direction="column" spacing={2} className={classes.table}>
      <Grid item container style={{ overflowX: 'auto' }}>
        <Table className={classes.root}>
          <TableHead>
            <TableRow >
              {
                cells.map((cell, i) => (
                  <TableCell key={i} style={{ width: 100 / props.columns.length + '%' }}>
                    <button
                      type="button"
                      onClick={() => Sort(i)}
                      style={{ display: 'flex', alignItems: 'center' }}
                    >
                      {cell.field}
                      {direct[i]}
                      {/* {cell.icon === 'sort'? <SortIcon /> : ''} */}
                    </button>
                  </TableCell>
                ))
              }
            </TableRow>
          </TableHead>
          <TableBody>
            {items.map((item, i) => (
              <TableRow key={i}>
                {
                  cells.map((cell, j) => {
                    const value = item[cell.key];
                    if (cell.key === 'control') {
                      if (max375) {
                        return (
                          <TableCell
                              onClick={(e) => {
                                setItem({ x: e.clientX, y: e.clientY - 15 });
                                setToggleMenu(!toggleMenu)
                              }}
                              className={classes.dotMenu}
                            >
                              <Menu
                                menuStyle={{
                                  position: "fixed",
                                  transform: `translate(${Item.x - 30}px, ${Item.y + 10
                                    }px)`,
                                  padding: 10,
                                  paddingBottom: 0,
                                  paddingTop: 0,
                                }}
                                menuButton={
                                  <Box className={classes.tableIcon}
                                    style={{marginLeft: "12px"}}
                                  >
                                    <Dots />
                                  </Box>
                                }
                              >
                                <MenuItem
                                  style={{ borderBottom: `1px solid ${theme.palette.border_color}`, backgroundColor: 'transparent'}}
                                  onClick={() => setCopied(item.address)}
                                >
                                  <LightTooltip title={copied ? "Copied!" : "Copy to Clipboard"} arrow>
                                    <Box
                                      display="flex" 
                                      alignItems="center" 
                                      justifyContent="space-around"
                                    >
                                      <CopyIcon width="20" height="20" className={classes.delete}/> 
                                      <Typography> Copy </Typography>
                                    </Box>
                                  </LightTooltip>
                                </MenuItem>
                                <MenuItem
                                  style={{backgroundColor: 'transparent'}}
                                  onClick={() => {
                                    setDeleteModalOpen(true);
                                    setSelectedItem(item)
                                  }}
                                >
                                  <LightTooltip title="Delete" arrow>
                                    <Box 
                                      display="flex" 
                                      alignItems="center" 
                                      justifyContent="space-around"
                                    >
                                      <DeleteIcon className={classes.delete}/> 
                                      <Typography> Delete </Typography>
                                    </Box>
                                  </LightTooltip>
                                </MenuItem>
                              </Menu>
                            </TableCell>
                        );
                      } else {
                        return (
                          <TableCell
                            key={j}
                            style={{ width: 100 / (props.columns.length) + '%' }}
                          >
                            <Box style={{display: 'flex', alignItems: 'center',
                              justifyContent: 'center'}}>
                              <LightTooltip title={copied ? "Copied!" : "Copy to Clipboard"} arrow>
                                <CopyIcon className={classes.link} onClick={() => setCopied(item.address)}/>
                              </LightTooltip>
                              <LightTooltip title="Delete" arrow>
                                <DeleteIcon className={classes.delete} onClick={() => {setDeleteModalOpen(true); setSelectedItem(item)}}/>
                              </LightTooltip>
                            </Box>
                          </TableCell>
                        );
                      }
                      
                    }
                    else if (cell.key === 'balance') {
                      return (
                        <TableCell
                          key={j}
                          style={{ width: 100 / (props.columns.length) + '%' }}
                        >
                          <Box className={classes.main_txt}>
                            {`$ ${value ? value : 0}`}
                          </Box>
                        </TableCell>
                      );
                    }
                    else {
                      return (
                        <TableCell
                          key={j}
                          style={{ width: 100 / (props.columns.length) + '%' }}
                        >
                          <Box style={{display: 'flex'}}>
                            <div className={classes.img}>
                              {
                                <img src={findWallet(item).src} />
                              }
                            </div>
                            <Box style={{display: 'grid'}}>
                              <Box className={classes.main_txt}>
                              {
                                item.type
                              }
                              </Box>
                              <Box className={classes.sub_txt}>
                              {
                                getReducedWalletAddress(item.address)
                              } 
                              </Box>
                            </Box>
                          </Box>
                        </TableCell>
                      );
                    }
                  })
                }
              </TableRow>
            ))}
          </TableBody>
          <TableFooter className={(props.tblFooter === 'true' || items.length === 0) ? classes.show : classes.hide}>
            {
              items.length === 0 &&
                <TableRow>
                  <TableCell colSpan="100%" style={{ textAlign: 'center', padding: "1rem 0" }}>No data found</TableCell>
                </TableRow>
            }
            </TableFooter>
        </Table>
      </Grid>
      <DeleteModal 
        open={deleteModalOpen}
        handleClose={() => setDeleteModalOpen(false)}
        selectedItem={selectedItem}
        handleDelete={() => {
          handleDelete(selectedItem.address)
        }}
      />
    </Grid>
  );
};
