import React from 'react';
import { Switch, Redirect, Route } from 'react-router-dom';

import { RouteWithLayout } from './components';
import { Main as MainLayout, Normal as NormalLayout, Auth as AuthLayout } from './layouts';

import {
  Dastboard as DashboardView,
  Swap as SwapView,
  Send as SendView,
  Wallet as WalletView,
  Address as AddressView,
  Nfts as NftsView,
  Education as EducationView,
  Settings as SettingsView,
  SignIn as SignInView,
  SignUp as SignUpView,
  MailVerification as MailVerificationView,
  ResetPassword as ResetPasswordView,
  ForgotPassword as ForgotPasswordView,
  NotFound as NotFoundView,
} from './views';

const Routes = () => {
  return (
    <Switch>
      <Redirect exact from="/" to="/login"/>
      <RouteWithLayout
        component={DashboardView}
        exact
        layout={MainLayout}
        title='Good Afternoon'
        description='Welcome to Clutch Connect, a powerful, secure and simple DeFi platform.'
        path="/dashboard"
      />
      <RouteWithLayout
        component={SwapView}
        exact
        layout={MainLayout}
        title='Swap'
        description='Easily swap one crypto for another'
        path="/swap"
      />
      <RouteWithLayout
        component={SendView}
        exact
        layout={MainLayout}
        title='Send Crypto'
        description="Send crypto to another wallet"
        path="/send"
      />
      <RouteWithLayout
        component={AddressView}
        exact
        layout={MainLayout}
        title='Address Book'
        description="Save the wallets you interact with"
        path="/address"
      />
      <RouteWithLayout
        component={NftsView}
        exact
        layout={MainLayout}
        title="NFT's"
        path="/nfts"
      />
      <RouteWithLayout
        component={WalletView}
        exact
        layout={MainLayout}
        title="Wallets"
        description="View and manage your connected wallets"
        path="/wallets"
      />
      <RouteWithLayout
        component={EducationView}
        exact
        layout={MainLayout}
        title="Education"
        path="/education"
      />
      <RouteWithLayout
        component={SettingsView}
        exact
        layout={MainLayout}
        title="Settings"
        path="/settings"
      />
      <RouteWithLayout
        component={SignInView}
        exact
        layout={AuthLayout}
        path="/login"
      />
      <RouteWithLayout
        component={SignUpView}
        exact
        layout={AuthLayout}
        path="/register"
      />
      <RouteWithLayout
        component={ForgotPasswordView}
        exact
        layout={AuthLayout}
        path="/forgot-password"
      />
      <RouteWithLayout
        component={ResetPasswordView}
        exact
        layout={AuthLayout}
        path="/reset-password/:token"
      />
      <RouteWithLayout
        component={MailVerificationView}
        exact
        layout={AuthLayout}
        path="/verify/:token" 
      />
      <RouteWithLayout
        component={NotFoundView}
        exact
        layout={NormalLayout}
        path="/not-found"
      />
      <Redirect to="/not-found" />
    </Switch>
  );
};

export default Routes;
