import React, { useState } from 'react';
import {Button, Box} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
//MuiGrid-root MuiGrid-container MuiGrid-item
const useStyles = makeStyles(theme => ({
  connect_button: {
    padding: 0,
    boxShadow: 'none',
    borderRadius: 9,
    border: `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.wallet_button_color,
    textTransform: 'none',
    justifyContent: 'flex-start !important',
    marginRight: 16,
    width: '100px',
    '&:hover': {
      backgroundColor: theme.palette.wallet_button_color,
      boxShadow: 'none'
    }
  },
  image: {
    width: 20,
    height: 20,
    margin: 'auto'
  },
  imageBox: {
    width: 40,
    height: 40,
    marginRight: 10,
    backgroundColor: theme.palette.wallet_button_color,
    borderRadius: 9,
    display: 'flex',
    alignItems: 'center'
  }
}));

export default function TokenCard(props) {
  const {tokenInfo, onClick} = props;
  const classes = useStyles();

  return (
    <Button variant="contained" className={classes.connect_button} onClick={onClick}>
      <Box className={classes.imageBox}>
        <img src={tokenInfo?.logoURI} className={classes.image} />
      </Box>
      <Box className={classes.title}>{tokenInfo?.symbol}</Box>
    </Button>
  );
}
