import moment from "moment";
import * as actionTypes from "../actions/ActionTypes";

const initialState = {
  walletList: [],
  currentWallet: '',
  currentWalletInfo: null,
  trendingData: [],
  addressList: []
};


const setWalletList = (state, {walletList, ...rest}) => {
  return {
    ...state,
    ...{
      walletList: walletList,
    },
  };
};

const setCurrentWallet = (state, {currentWallet, ...rest}) => {
  return {
    ...state,
    ...{
      currentWallet: currentWallet,
    },
  };
};

const setCurrentWalletInfo = (state, {currentWalletInfo, ...rest}) => {
  return {
    ...state,
    ...{
      currentWalletInfo: currentWalletInfo,
    },
  };
};

const setTrendingData = (state, {trendingData, ...rest}) => {
  return {
    ...state,
    ...{
      trendingData: trendingData,
    },
  };
};

const setAddressList = (state, {addressList, ...rest}) => {
  return {
    ...state,
    ...{
      addressList: addressList,
    },
  };
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_WALLET_LIST:
      return setWalletList(state, action);
    case actionTypes.SET_CURRENT_WALLET:
      return setCurrentWallet(state, action);
    case actionTypes.SET_CURRENT_WALLET_INFO:
      return setCurrentWalletInfo(state, action);
    case actionTypes.SET_TRENDING_DATA:
      return setTrendingData(state, action);
    case actionTypes.SET_ADDRESS_LIST:
      return setAddressList(state, action);
    default:
      return state;
  }
};

export default reducer;
