import React, { useEffect, useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { Drawer, Button } from '@material-ui/core';
import {
  AddressIcon,
	DashboardIcon,
	NftIcon,
	SendIcon,
	SettingIcon,
	LogoutIcon,
	SwapIcon,
	WalletIcon,
  EducationIcon
} from './svgs/icons';
import { SidebarNav } from './components';
import { withRouter, Link } from 'react-router-dom';
import useStyles from './style';

const items_normal = [
  {
    title: 'Home',
    href: '/dashboard',
    icon: <DashboardIcon/>
  },
  {
    title: 'Swap',
    href: '/swap',
    icon: <SwapIcon/>
  },
  {
    title: 'Send Crypto',
    href: '/send',
    icon: <SendIcon/>
  },
  {
    title: 'Wallets',
    href: '/wallets',
    icon: <WalletIcon/>
  },
  {
    title: 'Address Book',
    href: '/address',
    icon: <AddressIcon/>
  },
  {
    title: "NFT's",
    href: '/nfts',
    icon: <NftIcon/>
  },
  {
    title: "Education",
    href: '/education',
    icon: <EducationIcon/>
  },
];

const items_bottom = [
  {
    title: "Settings",
    href: '/settings',
    icon: <SettingIcon/>
  },
  {
    title: "Logout",
    href: '/login',
    icon: <LogoutIcon/>
  },
];
const Sidebar = props => {
  const { open, variant, history, onClose, className, ...rest } = props;
  const classes = useStyles();

  const logout = () => {
    history.push('/login');
  };

  return (
    <Drawer
      anchor="left"
      classes={{ paper: classes.drawer }}
      onClose={onClose}
      open={open}
      variant={variant}
    >
      <div
        // {...rest}
        className={clsx(classes.root, className)}
      >
        <Link to="/dashboard"  className={classes.logo}><img src="/images/Logo.svg" className={classes.main_logo}/></Link>
        <SidebarNav
          className={classes.nav}
          pages={items_normal}
          history={history}
          key="top"
        />
        <div className={classes.bottom}>
          <div className={classes.divider}/>
          <SidebarNav
            className={classes.nav}
            pages={items_bottom}
            history={history}
            key="bottom"
          />
        </div>
      </div>
    </Drawer>
  );
};

Sidebar.propTypes = {
  className: PropTypes.string,
  onClose: PropTypes.func,
  open: PropTypes.bool.isRequired,
  variant: PropTypes.string.isRequired
};

export default withRouter(Sidebar);
