import * as actionTypes from "./ActionTypes";

export const setWalletList = (payload) => ({
  type: actionTypes.SET_WALLET_LIST,
  walletList: payload
});

export const setCurrentWallet = (payload) => ({
  type: actionTypes.SET_CURRENT_WALLET,
  currentWallet: payload
});

export const setCurrentWalletInfo = (payload) => ({
  type: actionTypes.SET_CURRENT_WALLET_INFO,
  currentWalletInfo: payload
});

export const setTrendingData = (payload) => ({
  type: actionTypes.SET_TRENDING_DATA,
  trendingData: payload
});

export const setAddressList = (payload) => ({
  type: actionTypes.SET_ADDRESS_LIST,
  addressList: payload
});
