import { Box, Button, Dialog, IconButton } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import SearchInput from '../SearchInput';
import { CloseIcon } from './icons/icon';
import useStyles from './style';
import NetworkInfo from '../../config/tokens';
import TokenCard from '../TokenCard';
import { getBalanceWithMultiCall, getCorrectDecValue } from '../../web3/contracts';
import { MAIN_NETWORK_ID } from 'utils/constants';
import { useSelector } from 'react-redux';

const TokenSelectModal = props => {
  const { open, handleClose, handleSelectItem } = props;
  const classes = useStyles();
  const [searchStr, setSearchStr] = useState('');
  const [tokens, setTokens] = useState([]);
  const [latestTokens, setLatestTokens] = useState([]);
  const currentWallet = useSelector(state => state.app.currentWallet);
  const networkInfo = NetworkInfo[MAIN_NETWORK_ID]
  useEffect(() => {
    const filtToken = item => {
      if (
        item.symbol.toLowerCase().search(searchStr.toLowerCase()) >= 0 ||
        item.name.toLowerCase().search(searchStr.toLowerCase()) >= 0 ||
        item.address.toLowerCase() == searchStr.toLowerCase()
      )
        return true;
      return false;
    };
    let res = networkInfo.tokens.filter(filtToken);
    setTokens(res);
  }, [searchStr]);

  useEffect(() => {
    const getAllTokens = async () => {
      const res = await getBalanceWithMultiCall(networkInfo.tokens, currentWallet)

      let result = JSON.parse(JSON.stringify(networkInfo.tokens));
      if (res.status) {
        for (let i = 0; i < networkInfo.tokens.length; i ++) {
          result[i].balance = res.data[i];
        }
      }
      setTokens(result);
      setLatestTokens([result[0], result[1], result[2], result[3]])
    }
    getAllTokens()
  }, [currentWallet])

  return (
    <Dialog onClose={handleClose} open={open} className={classes.dialog}>
      <Box className={classes.header}>
        <Box>Select a token</Box>
        <Box className={classes.icon} onClick={handleClose}>
          <IconButton
            color="primary"
            aria-label="upload picture"
            component="span"
            className={classes.control_button}>
            <CloseIcon />
          </IconButton>
        </Box>
      </Box>
      <Box className={classes.searchbox}>
        <SearchInput
          placeholder="Search name or paste address"
          value={searchStr}
          onChange={e => setSearchStr(e.target.value)}
        />
      </Box>
      <Box className={classes.text}>LATEST TOKENS:</Box>
      <Box className={classes.latestToken}>
        {latestTokens.map((item, index) => (
          <TokenCard key={index} tokenInfo={item} onClick={() => {handleSelectItem(item); handleClose();}}/>
        ))}
      </Box>
      <Box className={classes.top_divider} />
      <Box className={classes.token_list}>
        {tokens.length == 0 ? (
          <Box className={classes.no_data}>No Results found.</Box>
        ) : (
          tokens.map((item, index) => (
            <Box key={index}>
              <Box
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  margin: '16px 0px'
                }}>
                <Box style={{ display: 'flex', alignItems: 'center' }}>
                  <TokenCard tokenInfo={item} onClick={() => {handleSelectItem(item); handleClose();}}/>
                  <Box className={classes.name}>{item.name}</Box>
                </Box>
                <Box style={{ textAlign: 'right' }}>
                  <Box className={classes.description}>Your balance:</Box>
                  <Box className={classes.value}>
                    {item.balance === undefined
                      ? '-'
                      : `${getCorrectDecValue(item.balance, item.decimals, 3)} ${item.symbol}`}
                  </Box>
                </Box>
              </Box>
              <Box className={classes.divider} />
            </Box>
          ))
        )}
      </Box>
    </Dialog>
  );
};

export default TokenSelectModal;
